.accordion {
  .accordion__item {
    margin-bottom: 15px;
    .accordion__button {
      box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
      border-top-left-radius: 10px;
      cursor: pointer;
      outline: none;
    }
  }
  .active {
    span {
      background: $sub;
      display: block;
      height: 100%;
      color: white;
    }
    outline: none;
  }
}
